.addUserForm {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.users {
  &__list {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    padding: 10px;
    gap: 15px;
    list-style-type: none;

    & li {
      border: 1px solid black;
      padding: 10px;


      & a {
        margin-bottom: 5px;
        margin-right: 10px;
      }
    }
  }
}